:root {
  --primaryBlue: hsl(208, 100%, 34%);
  --darkBlue: hsl(207, 100%, 13%);
  --medBlue: hsl(208, 57%, 47%);
  --lightBlue: hsl(195, 66%, 71%) ;
  --mutedBlue: hsl(208, 33%, 60%) ;
  --primaryGreen: hsl(92, 51%, 51%);
  --darkGreen: hsl(92, 50%, 20%);
  --medGreen: hsl(92, 51%, 66%);
  --lightGreen: hsl(79, 45%, 24%);
  --mutedGreen: hsl(92, 37%, 71%);
  --primaryOrange: hsl(32, 94%, 60%);
  --darkOrange:	hsl(32, 62%, 38%);
  --medOrange: hsl(0, 0%, 0%);
  --lightOrange: hsl(32, 95%, 84%);
  --mutedOrange: hsl(32, 61%, 76%);
  --primaryYellow: hsl(49, 100%, 50%);
  --darkYellow:	hsl(49, 100%, 30%);
  --medYellow: hsl(49, 100%, 65%);
  --lightYellow: hsl(49, 100%, 80%);
  --mutedYellow: hsl(48, 54%, 70%);
  --primaryRed: hsl(347, 100%, 45%);
  --darkRed: hsl(347, 100%, 27%);
  --medRed:	hsl(347, 83%, 56%);
  --lightRed: hsl(347, 82%, 78%);
  --mutedRed: hsl(347, 54%, 67%);
  --weFinancial: hsl(193, 100%, 42%);
  --weFinancialDark: hsl(207, 100%, 26%);
  --lightGrey: hsl(0, 0%, 81%);
  --medGrey: hsl(0, 0%, 53%);
  --dkGrey: hsl(0, 0%, 25%);
  --black: hsl(0, 0%, 6%);
  --textBlack: hsl(0, 0%, 16%);
  --white: hsl(45, 22%, 96%);
  --white60: hsla(240, 100%, 99%, .85);
}

:root {
  --font-heading: Helvetica, Arial, sans-serif; /* heading font */
  --font-text: "Atkinson Hyperlegible", Helvetica, Arial, sans-serif; /* body font */
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-heading);
  line-height: 1.1;
  margin: 0 0 1.25rem;
}

h1 {
  font-size: 2.5rem;
  font-size: clamp(2.5rem, 0.9375rem + 3.35vw, 3.5rem);
}

h2 {
  font-size: 1.875rem;
  font-size: clamp(1.875rem, 1.6875rem + 0.78vw, 2.625rem);
  font-weight: 500;
}

h3 {
  font-size: 1.25rem;
  font-size: clamp(1.25rem, 1.1875rem + 0.26vw, 1.5rem);
  font-weight: 500;
  margin-bottom: 0;
}

p {
  font-size: 1rem;
  font-weight: 400;
  font-style: normal;
  margin-top: 0;
  line-height: 1.5rem;
}

.large {
  font-size: 1.25rem;
  font-weight: 400;
  font-style: normal;
  margin-top: 0;
  line-height: 1.625rem;
}

.small {
  font-size: 0.75rem;
  font-weight: 400;
  font-style: normal;
  margin-top: 0;
  line-height: 1.5rem;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-text);
  background-color: var(--white);
  color: var(--black);
}

img {
  display: block;
  max-width: 100%;
}

figure,
blockquote {
  margin: 0;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}
.visually-hidden:focus {
  left: 0;
  width: auto;
  height: auto;
  background-color: #fff;
  padding: 0.5rem;
  border: 1px solid;
}

:root {
  --wrapper-padding-inline: 1rem;
  --wrapper-max-width: 1280px;
  --wrapper-padding-block: 32px;
}

.header {
  width: 10vw;
  height: auto;
  background-color: var(--white);
  box-shadow: 0 0 1.5rem var(--black);
  z-index: 50;
}
.header__site {
  width: 100%;
  height: auto;
  position: fixed;
  background-color: var(--white);
  box-shadow: 0 0 1.5rem var(--black);
}

.site__branding {
  display: grid;
  grid-column: 1;
  grid-row: 1/4;
  z-index: 30;
  background-color: var(--primaryBlue);
  padding: 40px 15px 0 15px;
  height: 196px;
  width: 100%;
  max-width: 196px;
  box-shadow: 0 0 10px var(--black);
}
.site__branding a {
  grid-row: 4/4;
}
.site__navigation {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  height: 144px;
}

.topnav {
  margin: 0;
}
.topnav__wrapper {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-height: 9rem;
  overflow: visible;
  position: relative;
  z-index: 400;
}
.topnav__links {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  gap: 2.5rem;
  list-style-type: none;
  margin-bottom: 32px;
  padding: 0;
}
@media (max-width: 58.124375em) {
  .topnav__links {
    flex-direction: column;
    align-items: left;
  }
}
.topnav__link {
  display: block;
  font-size: 1.125rem;
  line-height: 1;
  font-weight: 400;
  color: var(--primaryBlue);
  text-decoration: none;
}
.topnav__link:hover {
  text-decoration: underline;
  color: var(--darkOrange);
  cursor: pointer;
}

.mySearch {
  width: 400px;
}

.topLinks {
  display: flex;
  flex-direction: row;
  gap: 3rem;
  list-style-type: none;
  margin-top: 24px;
  padding: 0;
  color: var(--primaryBlue);
}
.topLinks__lang--active {
  font-weight: 700;
  text-transform: uppercase;
}
.topLinks__location, .topLinks__email, .topLinks__flyer {
  display: flex;
  flex-direction: row;
  gap: 8px;
}

.hero__section {
  background-color: var(--darkBlue);
}
.hero__section--intro {
  height: 90vh;
  margin-bottom: 9rem;
}
.hero__text {
  font-size: 90px;
  line-height: 1;
  color: var(--white);
  border-left: 20px solid var(--white);
  padding-left: 25px;
  margin-bottom: 0;
  display: block;
}
.hero__image {
  height: 90vh;
  object-fit: cover;
  margin-top: 9rem;
}

.hero.full-width-split-screen {
  padding-top: 144px;
}

.full-width-split-screen {
  padding-block: 0;
}

.full-width-split-screen > :not(img) {
  padding-block: var(--wrapper-padding-block);
  padding-inline: var(--wrapper-padding-inline);
}

@media (min-width: 930px) {
  .full-width-split-screen {
    display: grid;
    grid-template-columns: minmax(var(--wrapper-padding-inline), 1fr) minmax(0, calc(var(--wrapper-max-width) / 2)) minmax(0, calc(var(--wrapper-max-width) / 2)) minmax(var(--wrapper-padding-inline), 1fr);
    position: relative;
  }
  .full-width-split-screen > img:first-child {
    grid-column: 1/3;
  }
  .full-width-split-screen > img:last-child {
    grid-column: 3/-1;
  }
  .full-width-split-screen > :not(img) {
    display: grid;
    align-content: center;
    justify-items: start;
  }
  .full-width-split-screen > :not(img):first-child {
    grid-column: 2/3;
    padding-left: 0;
    padding-right: clamp(1rem, 5vw, 3rem);
  }
  .full-width-split-screen > :not(img):last-child {
    grid-column: 3/4;
    padding-right: 0;
    padding-left: clamp(1rem, 5vw, 3rem);
  }
  .full-width-split-screen > :not(img)::before {
    content: "";
    position: absolute;
    inset: 0;
    grid-row: 1/2;
    background-color: inherit;
  }
  .full-width-split-screen > :not(img):first-child::before {
    grid-column: 1/2;
  }
  .full-width-split-screen > :not(img):last-child::before {
    grid-column: 4/5;
  }
}
.wrapper {
  max-width: var(--wrapper-max-width);
  margin-inline: auto;
  padding-inline: var(--wrapper-padding-inline);
  padding-block: var(--wrapper-padding-block);
}
.wrapper__header {
  max-width: 80rem;
  width: 100%;
  height: 144px;
  margin: 0 auto;
  display: grid;
  grid-auto-columns: 1fr auto;
  position: relative;
  padding: 0 1.5rem;
}
@media (max-width: 58.124375em) {
  .wrapper__header {
    grid-template-rows: 40px 65px 2.2rem;
  }
}
.wrapper__navigation {
  background: transparent;
  display: grid;
  grid-row: 1/3;
  grid-column: 2/2;
  grid-template-columns: auto auto;
  grid-template-rows: 40px 70px;
  height: 144px;
  margin-left: 0;
  position: relative;
  width: auto;
}